@charset "UTF-8";
/**
 * myreset.css
 * 
 * 2017730
 * 1. 
 *       2. reset 
 *       3. reset 
 */
/*  */
body, h1, h2, h3, h4, h5, h6, hr, p, blockquote,
pre, dl, dt, dd, ul, ol, li,
form, fieldset, lengend, button, input, textarea,
th, td {
  margin: 0;
  padding: 0; }

/*  */
ul, ol {
  list-style: none; }

/*  */
a {
  text-decoration: none; }

q:before, q:after {
  content: ''; }

/*  */
legend {
  color: #000; }

/* for ie6 */
button, textarea {
  font-size: 100%;
  border: 0; }

/*  ie  */
fieldset, img {
  border: 0; }

/* img  img  */
/*  */
table {
  border-collapse: collapse;
  border-spacing: 0; }

/*  */
.fl {
  float: left; }

.fr {
  float: right; }

.clearfix:after {
  display: block;
  clear: both;
  content: "";
  visibility: hidden;
  height: 0; }

.clearfix {
  zoom: 1; }

/*  */
::selection {
  background-color: #b3d4fc;
  text-shadow: none; }

/*  */
.browser-up {
  padding: 1rem;
  background: #ccc;
  text-align: center; }

/*  */
body {
  /* -webkit-text-size-adjust: none; */
  /* -webkit-tap-highlight-color: rgba(0, 0, 0, 0); */ }

body, html {
  line-height: 1;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif, 'Droid Sans Fallback';
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  background: #222;
  color: #fff; }

@font-face {
  font-family: 'icomoon';
  src: url("../fonts/icomoon.eot?2qevqt");
  src: url("../fonts/icomoon.eot?2qevqt#iefix") format("embedded-opentype"), url("../fonts/icomoon.ttf?2qevqt") format("truetype"), url("../fonts/icomoon.woff?2qevqt") format("woff"), url("../fonts/icomoon.svg?2qevqt#icomoon") format("svg");
  font-weight: normal;
  font-style: normal; }

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

